<template>
    <nacl-page-container no-footer class="time-draw" no-header>
        <!-- <nacl-header style="color: #fff;background:linear-gradient(75deg,rgba(134,40,250,1) 0%,rgba(81,47,253,1) 100%);">
            {{ title[type] }}
        </nacl-header> -->
        <div class="bg-1">
            <img src="../assets/timedraw-bg-1.png" alt="">
        </div>
        <div class="bg-2">
            <img src="../assets/timedraw-bg-2.png" alt="">
            <!-- <p class="text" @click="lottery">00:10:31</p> -->
            <p v-if="countdown.m < 55 && type === '0'" class="text">{{ countdown.h }}:{{ countdown.m }}:{{ countdown.s }}</p>
            <p v-else-if="countdown.m > 55 && type === '0' && isHour" class="text">本小时已抽奖</p>
            <p v-else-if="type === '1' && isDay" class="text">本日已抽奖</p>
            <p v-else-if="type === '2' && isWeek" class="text">本周已抽奖</p>
            <p v-else class="text" @click="lottery">立刻抽奖</p>
        </div>
        <div class="my-prize"><a @click="goWinningRecord()"><img src="../assets/my-prize.png" alt=""></a></div>
        <div class="rule">
            <p v-if="type === '0'">1、整点抽大奖：每个整点后五分钟内，登陆用户拥有一次免费抽奖机会</p>
            <p v-if="type === '1'">1、天天抽大奖：每日00:00:00至23:59:59时段内，登陆用户拥有一次免费抽奖机会</p>
            <p v-if="type === '2'">1、整点抽大奖：每周一开始至周日时间段内，登陆用户拥有一次免费抽奖机会</p>
            <p>2、中奖所得优惠卷可用于所有满足金额条件的商品</p>
            <p>3、中奖所得商品无需额外支付任何费用</p>
            <p>4、本活动最终解释权归本公司所有</p>
        </div>
        <div v-if="isShowWinning" class="winning">
            <div class="winning-bg">
                <img src="../assets/winning.png" alt="">
                <img class="content" :src="activeData.couponimg" alt="">
            </div>
            <img class="winning-close" src="../assets/winning-close.png" @click="isShowWinning = false">
        </div>
    </nacl-page-container>
</template>

<script>
import { getFindlimit, addzj, getUserData, couponReceive } from '../services'

export default {
    name: 'timedraw',
    components: {
    },
    data () {
        // console.log(typeof this.$route.query.type)
        const userData = getUserData() || {}
        return {
            listData: [],
            activeData: {},
            isShowWinning: false,
            countdown: {
                h: '00',
                m: '00',
                s: '00'
            },
            type: this.$route.query.type,
            userData: getUserData(),
            hourDate: localStorage.getItem(`hour-${userData.userId}`),
            dayDate: localStorage.getItem(`day-${userData.userId}`),
            weekDate: localStorage.getItem(`week-${userData.userId}`),
            title: ['整点抽大奖', '天天抽大奖', '周周抽大奖']
        }
    },
    computed: {
        isHour () {
            if (!this.hourDate) return false
            else {
                const saveTIme = new Date(this.hourDate)
                const dateEnd = new Date()
                const difValue = (dateEnd - saveTIme) / (1000 * 60 * 60 * 1)
                return difValue < 1
            }
        },
        isDay () {
            if (!this.dayDate) return false
            else {
                const saveTIme = new Date(this.dayDate)
                const dateEnd = new Date()
                const difValue = (dateEnd - saveTIme) / (1000 * 60 * 60 * 24)
                return difValue < 24
            }
        },
        isWeek () {
            if (!this.weekDate) return false
            else {
                const oneDayTime = 1000 * 60 * 60 * 24
                const saveTIme = new Date(this.weekDate)
                const dateEnd = new Date()
                const saveTImeCount = parseInt(saveTIme.getTime() / oneDayTime)
                const dateEndCount = parseInt(dateEnd.getTime() / oneDayTime)
                return parseInt((saveTImeCount + 4) / 7) === parseInt((dateEndCount + 4) / 7)
            }
        }
    },
    async created () {
        this.setCountdown()
        this.listData = await getFindlimit()
        console.log(this.listData)
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        goWinningRecord(){
            this.toGetUser();
            window.webkit.messageHandlers.winningRecord.postMessage('');
        },
        async lottery () {
            if (!this.userData) {
                this.toGetUser();
                window.webkit.messageHandlers.login.postMessage('');
                // this.$router.push('/login')
            }
            const nub = Math.ceil(Math.random() * 10)
            if (nub <= 1) {
                this.activeData = this.listData[3]
            } else if (nub <= 3 && nub > 1) {
                this.activeData = this.listData[2]
            } else if (nub < 6 && nub > 3) {
                this.activeData = this.listData[1]
            } else {
                this.activeData = this.listData[0]
            }
            console.log(this.activeData)
            this.isShowWinning = true
            await addzj({
                zjname: this.activeData.couponname,
                zjimg: this.activeData.couponimg
            })
            await couponReceive(this.activeData.couponid)
            const date = new Date()
            if (this.type === '0') {
                let timestamp = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + date.getHours() + ':00'
                localStorage.setItem(`hour-${this.userData.userId}`, new Date(timestamp))
                this.hourDate = new Date(timestamp)
            } else if (this.type === '1') {
                let timestamp = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate()
                localStorage.setItem(`day-${this.userData.userId}`, new Date(timestamp))
                this.dayDate = new Date(timestamp)
            } else if (this.type === '2') {
                localStorage.setItem(`week-${this.userData.userId}`, date)
                this.weekDate = date
            }
        },
        setCountdown (type) {
            let timestamp = new Date().getTime()
            timestamp = timestamp + 1 * 60 * 60 * 1000
            let dateAfter = new Date(timestamp)
            let endTime = dateAfter.getFullYear() + '-' + (dateAfter.getMonth() + 1) + '-' + dateAfter.getDate() + ' ' + dateAfter.getHours() + ':00'
            // 目标日期时间戳
            const end = Date.parse(new Date(endTime))
            // 当前时间戳
            const now = Date.parse(new Date())
            // 相差的毫秒数
            const msec = end - now
            // 计算时分秒数
            let day = parseInt(msec / 1000 / 60 / 60 / 24)
            let hr = parseInt(msec / 1000 / 60 / 60 % 24)
            let min = parseInt(msec / 1000 / 60 % 60)
            let sec = parseInt(msec / 1000 % 60)
            // 个位数前补零
            hr = hr > 9 ? hr : '0' + hr
            min = min > 9 ? min : '0' + min
            sec = sec > 9 ? sec : '0' + sec
            this.countdown = {
                d: day,
                h: hr,
                m: min,
                s: sec
            }
            setTimeout(() => this.setCountdown(), 1000)
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>
